import { Box, Button, Container, Typography } from "@mui/material";
import React from "react";
import WestIcon from "@mui/icons-material/West";
import PlaceOutlinedIcon from "@mui/icons-material/PlaceOutlined";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import SyncAltOutlinedIcon from "@mui/icons-material/SyncAltOutlined";
import ReplyOutlinedIcon from "@mui/icons-material/ReplyOutlined";

export default function PropertyHero() {
  return (
    <Box
      sx={{
        backgroundImage: "url(/images/PropertyDetailsBG.svg)",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        height: "500px",
        width: "100%",
      }}
    >
      <Container maxWidth="xl" sx={{ px: { lg: 7, xs: 1 } }}>
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
            height: "450px",
          }}
        >
          <Box sx={{ display: "flex", justifyContent: "flex-end", p: 2 }}>
            <Button
              startIcon={<WestIcon sx={{ fontSize: "30px" }} />}
              variant="contained"
              sx={{
                backgroundColor: "#E0D8C3",
                "&:hover": {
                  backgroundColor: "#4D4D4D",
                  color: "#fff",
                },
                color: "#000",
                minWidth: "233px",
                height: "53px",
                textTransform: "none",
              }}
            >
              Back To Search
            </Button>
          </Box>
          <Box sx={{display: "flex", flexWrap: "wrap", justifyContent: "space-between"}}>
            <Box>
              <Typography
                sx={{ fontSize: "37px", fontWeight: 600, color: "#fff" }}
              >
                Diamond Manor Apartment
              </Typography>
              <Typography
                variant="body2"
                sx={{
                  display: "flex",
                  alignItems: "center",
                  mb: 1,
                  color: "#fff",
                }}
              >
                <PlaceOutlinedIcon fontSize="small" sx={{ mr: 0.5 }} />
                22-05 Astoria Blvd, Astoria
              </Typography>
            </Box>
            <Box>
                <Typography sx={{fontSize: "37px", color: "#fff", fontWeight: 600}}>$6500</Typography>
                  
            </Box>
          </Box>
        </Box>
      </Container>
    </Box>
  );
}
